/**
 * Sayfanƒ±n yukarƒ±sƒ±na √ßƒ±kmak i√ßin scroll-up buton i≈ülevselliƒüi
 */

// Scroll Up butonunu olu≈ütur ve sayfaya ekle
function createScrollUpButton() {
  // Buton HTML'i olu≈ütur
  const button = document.createElement('button');
  button.className = 'scroll-up';
  button.id = 'scroll-up-button';
  button.setAttribute('aria-label', 'Sayfanƒ±n √ºst√ºne √ßƒ±k');
  button.setAttribute('title', 'Sayfanƒ±n √ºst√ºne √ßƒ±k');
  
  // SVG ikon ekle
  button.innerHTML = `
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
      <path d="M12 4l8 8h-6v8h-4v-8H4z"/>
    </svg>
  `;
  
  // Sayfanƒ±n sonuna ekle
  document.body.appendChild(button);
  
  // Buton tƒ±klama olayƒ±nƒ± ekle
  button.addEventListener('click', scrollToTop);
  
  // Sayfa kaydƒ±rma olayƒ±nƒ± izle
  window.addEventListener('scroll', toggleScrollButton);
}

// Sayfa yukarƒ±ya kaydƒ±rma fonksiyonu
function scrollToTop() {
  // Yumu≈üak ge√ßi≈üle sayfa ba≈üƒ±na git
  window.scrollTo({
    top: 0,
    behavior: 'smooth'
  });
}

// Scroll butonunun g√∂r√ºn√ºrl√ºƒü√ºn√º kontrol et
function toggleScrollButton() {
  const button = document.getElementById('scroll-up-button');
  if (!button) return;
  
  // Sayfanƒ±n belirli bir kƒ±smƒ±ndan sonra butonu g√∂ster (300px)
  if (window.pageYOffset > 300) {
    button.classList.add('visible');
  } else {
    button.classList.remove('visible');
  }
}

// Sayfa y√ºklendiƒüinde scroll-up butonunu olu≈ütur
document.addEventListener('DOMContentLoaded', () => {
  createScrollUpButton();
});

export { createScrollUpButton, scrollToTop, toggleScrollButton }; 