/**
 * Loader i≈ülevselliƒüi i√ßin yardƒ±mcƒ± fonksiyonlar
 */

// Film/sinema ile ilgili y√ºkleme mesajlarƒ±
const loadingMessages = [
  "Filminiz y√ºkleniyor...",
  "Sinema perdesi hazƒ±rlanƒ±yor...",
  "Filminiz ba≈ülamak √ºzere...",
  "Koltuklar hazƒ±rlanƒ±yor...",
  "Patlamƒ±≈ü mƒ±sƒ±rlar hazƒ±r...",
  "Film makarasƒ± takƒ±lƒ±yor...",
  "I≈üƒ±klar kapanƒ±yor...",
  "Film ba≈ülamak √ºzere..."
];

// Loader'ƒ± g√∂sterme fonksiyonu
export function showLoader() {
  const loader = document.getElementById('loader-container');
  if (loader) {
    // Rastgele bir film mesajƒ± se√ß
    const randomMessage = loadingMessages[Math.floor(Math.random() * loadingMessages.length)];
    const messageElement = loader.querySelector('.loader-text');
    if (messageElement) {
      messageElement.textContent = randomMessage;
    }
    
    loader.classList.add('active');
  }
}

// Loader'ƒ± gizleme fonksiyonu
export function hideLoader() {
  const loader = document.getElementById('loader-container');
  if (loader) {
    loader.classList.remove('active');
  }
}

// API isteƒüi yapmak i√ßin yardƒ±mcƒ± fonksiyon
export async function fetchWithLoader(url, options) {
  try {
    showLoader();
    const response = await fetch(url, options);
    const data = await response.json();
    return data;
  } catch (error) {
    console.error('Fetch error:', error);
    throw error;
  } finally {
    // Animasyonun en az 1 saniye g√∂r√ºnmesini saƒüla
    setTimeout(() => {
      hideLoader();
    }, 1000);
  }
} 