function getGenreNames(genreIds) {
    const genreMap = {
        28: 'Action',
        12: 'Adventure',
        16: 'Animation',
        35: 'Comedy',
        80: 'Crime',
        99: 'Documentary',
        18: 'Drama',
        10751: 'Family',
        14: 'Fantasy',
        36: 'History',
        27: 'Horror',
        10402: 'Music',
        9648: 'Mystery',
        10749: 'Romance',
        878: 'Science Fiction',
        10770: 'TV Movie',
        53: 'Thriller',
        10752: 'War',
        37: 'Western',
    };
    if (!genreIds || !Array.isArray(genreIds)) return '';
    const mappedGenres = genreIds.map(id => genreMap[id] || 'Unknown');
    return mappedGenres.slice(0, 2).join(', '); // Maksimum 2 genre d√∂nd√ºr
}